body {
  background: #23110f;
  font-family: "Muli";
}

.hm {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  flex-flow: column;
}

.hm a {
  width: 100%;
  top: 25%;
  left: 3%;
  max-width: 240px;
  height: 54px;
  padding: 8px;
  font-size: 0.8rem;
  font-weight: 900;
  color: #ff4655;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  margin: 10px 0;
}

.hm a.white:hover > p {
  color: #e3ece1;
}

.hm a.white > p {
  background: #e3ece1;
  color: #23110f;
}

.hm a.white > p span.base {
  border: 1px solid transparent;
}

.hm a.transparent:hover > p {
  color: #e3ece1;
}

.hm a.transparent:hover > p span.text {
  box-shadow: 0 0 0 1px #e3ece1;
}

.hm a.transparent > p {
  background: #23110f;
  color: #e3ece1;
}

.hm a.transparent > p span.base {
  border: 1px solid #e3ece1;
}

.hm a:after, .hm a:before {
  content: "";
  width: 1px;
  position: absolute;
  height: 8px;
  background: #23110f;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.hm a:before {
  right: 0;
  left: auto;
  left: initial;
}

.hm a p {
  margin: 0;
  height: 54px;
  line-height: 54px;
  box-sizing: border-box;
  z-index: 1;
  left: 0;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.hm a p span.base {
  box-sizing: border-box;
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  left: 0;
  border: 1px solid #ff4655;
}

.hm a p span.base:before {
  content: "";
  width: 2px;
  height: 2px;
  left: -1px;
  top: -1px;
  background: #23110f;
  position: absolute;
  transition: 0.3s ease-out all;
}

.hm a p span.bg {
  left: -5%;
  position: absolute;
  background: #ff4655;
  width: 0;
  height: 100%;
  z-index: 3;
  transition: 0.3s ease-out all;
  transform: skewX(-10deg);
}

.hm a p span.text {
  z-index: 4;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.hm a p span.text:after {
  content: "";
  width: 4px;
  height: 4px;
  right: 0;
  bottom: 0;
  background: #23110f;
  position: absolute;
  transition: 0.3s ease-out all;
  z-index: 5;
}

.hm a:hover {
  color: #e3ece1;
}

.hm a:hover span.bg {
  width: 110%;
}

.hm a:hover span.text:after {
  background: #e3ece1;
}

